'use strict';


var Options = require('./Options');
var common = require('../common');

exports.read = read;
exports.getByteLength = Options.getByteLength;
exports.getArgumentCount = Options.getArgumentCount;

function read(part) {
  return Options.read(part, common.TransactionFlagName);
}